import * as React from "react"

const SvgComponent = (props) => (
  <svg
    xmlns="http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
    width={21}
    height={21}
    {...props}
  >
    <image
      width={21}
      height={21}
      xlinkHref="data:image/png;base64,"
    />
  </svg>
)

export default SvgComponent
